  font-weight: bold;
  font-size: 24px;
  cursor: default;
}

/* INPUTS E TEXTAREAS */
.input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #626567;
  font-weight: bold;
  background-color: #cacfd2;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  cursor: default;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.textarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 85px;
  resize: none;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  text-align: left;
  text-transform: uppercase;
  color: #626567;
  font-weight: bold;
  background-color: #cacfd2;
  border-width: 5px;
  border-color: #cacfd2;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  cursor: default;
}

.textarea::-webkit-scrollbar {
  width: 20px;
}

.textarea::-webkit-scrollbar-track {
  background-color: #bdc3c7;
  border-radius: 5px;
}

.textarea::-webkit-scrollbar-button {
  display: none;
}

.textarea::-webkit-scrollbar-corner {
  display: none;
}

.textarea::-webkit-scrollbar-thumb {
  background-color: #a6acaf;
  border-radius: 5px;
}

