/* ANIMAÇÕES */
.fade-in {
  animation-name: fadein;
  animation-duration: 0.5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fade-out {
  animation-name: fadeout;
  animation-duration: 0.5s;
  animation-delay: 2s;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.body-animation {
  animation-name: bodyanimation;
  animation-duration: 3s;
}

@keyframes bodyanimation {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.toast {
  animation: toast 3s ease forwards;
}

@keyframes toast {
  0%,
  100% {
    opacity: 0;
  }
  10%,
  90% {
    opacity: 1;
  }
}

/* RADIO */
.radio-toolbar input[type='radio'] {
  opacity: 0;
  position: fixed;
  width: 0;
  cursor: default;
}

.radio-toolbar label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #ffffff;
  font-weight: bold;
  min-width: 50px;
  min-height: 50px;
  height: 50px;
  width: 100px;
  background-color: #7fb3d5;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 0px;
  margin-bottom: 5px;
  margin-left: 2.5px;
  margin-right: 2.5px;
  cursor: default;
}

.radio-toolbar input[type='radio']:focus + label {
  background-color: #5499c7;
  cursor: default;
}

.radio-toolbar input[type='radio']:checked + label {
  background-color: #ec7063;
  cursor: default;
}

/* CHECKBOX */
