.alert {
  display: flex;
  flex-direction: flex-start;
  text-align: center;
  overflow-y: scroll;
  background-color: #f1948a;
  border-style: solid;
  border-width: 5px;
  border-color: #f1948a;
  border-radius: 5px;
  box-shadow: transparent;
  cursor: default;
  font-weight: bold;
  color: #e74c3c;
}

.alert::-webkit-scrollbar {
  width: 20px;
}

.alert::-webkit-scrollbar-track {
  background-color: #ec7063;
  border-radius: 5px;
}

.alert::-webkit-scrollbar-button {
  display: none;
}

.alert::-webkit-scrollbar-corner {
  display: none;
}

.alert::-webkit-scrollbar-thumb {
  background-color: #e74c3c;
  border-radius: 5px;
}

